"use client"; // Este componente usa hooks y se renderiza en cliente

import React from "react";

interface ParameterSelectorProps {
  parameters: {
    layerHeight: number;
    infillPercentage: number;
    support: boolean;
  };
  onChange: (params: {
    layerHeight: number;
    infillPercentage: number;
    support: boolean;
  }) => void;
}

/**
 * Componente para seleccionar parámetros de corte (slicing)
 */
export default function ParameterSelector({ parameters, onChange }: ParameterSelectorProps) {
  // Función para manejar cambios en inputs numéricos
  function handleInputChange(e: React.ChangeEvent<HTMLInputElement>) {
    const { name, value, type, checked } = e.target;
    if (type === "checkbox") {
      onChange({ ...parameters, [name]: checked });
    } else {
      onChange({ ...parameters, [name]: Number(value) });
    }
  }

  return (
    <div className="space-y-4">
      <h2 className="text-xl font-semibold mb-2">Parámetros de impresión</h2>

      {/* Selector de altura de capa */}
      <div>
        <label htmlFor="layerHeight" className="block font-medium mb-1">
          Altura de capa (mm)
        </label>
        <input
          type="number"
          id="layerHeight"
          name="layerHeight"
          min={0.05}
          max={0.5}
          step={0.01}
          value={parameters.layerHeight}
          onChange={handleInputChange}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
      </div>

      {/* Selector de porcentaje de relleno */}
      <div>
        <label htmlFor="infillPercentage" className="block font-medium mb-1">
          Porcentaje de relleno (%)
        </label>
        <input
          type="number"
          id="infillPercentage"
          name="infillPercentage"
          min={0}
          max={100}
          step={1}
          value={parameters.infillPercentage}
          onChange={handleInputChange}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
      </div>

      {/* Checkbox para soporte */}
      <div className="flex items-center space-x-2">
        <input
          type="checkbox"
          id="support"
          name="support"
          checked={parameters.support}
          onChange={handleInputChange}
          className="h-4 w-4"
        />
        <label htmlFor="support" className="font-medium">
          Incluir soporte
        </label>
      </div>
    </div>
  );
}
