"use client";

import React, { useRef, useState } from "react";

interface ModelUploaderProps {
  onModelUpload: (file: File | null) => void;
}

/**
 * Componente para subir archivos STL, OBJ o 3MF con soporte drag & drop
 */
export default function ModelUploader({ onModelUpload }: ModelUploaderProps) {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [error, setError] = useState<string | null>(null);
  const [dragActive, setDragActive] = useState(false);

  // Tipos de archivo permitidos
  const acceptedTypes = [".stl", ".obj", ".3mf"];
  const acceptedTypesText = "STL, OBJ o 3MF";

  // Función para validar el archivo seleccionado
  function validateFile(file: File) {
    const extension = file.name.toLowerCase().slice(file.name.lastIndexOf("."));
    if (!acceptedTypes.includes(extension)) {
      setError(`Formato no soportado. Por favor, sube un archivo ${acceptedTypesText}.`);
      onModelUpload(null);
      return false;
    }
    setError(null);
    return true;
  }

  // Maneja la selección de archivo desde el input
  function handleFileChange(e: React.ChangeEvent<HTMLInputElement>) {
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      if (validateFile(file)) {
        onModelUpload(file);
      }
    }
  }

  // Maneja el drop de archivo en la zona drag & drop
  function handleDrop(e: React.DragEvent<HTMLDivElement>) {
    e.preventDefault();
    setDragActive(false);
    if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      const file = e.dataTransfer.files[0];
      if (validateFile(file)) {
        onModelUpload(file);
      }
      e.dataTransfer.clearData();
    }
  }

  // Previene el comportamiento por defecto para drag & drop
  function handleDragOver(e: React.DragEvent<HTMLDivElement>) {
    e.preventDefault();
    setDragActive(true);
  }

  function handleDragLeave(e: React.DragEvent<HTMLDivElement>) {
    e.preventDefault();
    setDragActive(false);
  }

  return (
    <div>
      <label className="block mb-2 font-semibold">Sube tu modelo 3D ({acceptedTypesText}):</label>

      {/* Zona drag & drop */}
      <div
        className={`border-2 border-dashed rounded-lg p-6 text-center cursor-pointer transition-colors ${
          dragActive ? "border-black bg-gray-100" : "border-gray-400 hover:border-gray-600"
        }`}
        onDrop={handleDrop}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onClick={() => fileInputRef.current?.click()}
      >
        <p className="text-gray-600">
          Arrastra y suelta tu archivo {acceptedTypesText} aquí<br />
          o haz clic para seleccionar
        </p>
      </div>

      {/* Input oculto para seleccionar archivo */}
      <input
        type="file"
        accept={acceptedTypes.join(",")}
        ref={fileInputRef}
        className="hidden"
        onChange={handleFileChange}
      />

      {/* Mensaje de error */}
      {error && (
        <p className="mt-2 text-red-600 font-medium">
          {error}
        </p>
      )}
    </div>
  );
}
