"use client"; // Este componente usa hooks y se renderiza en cliente

import React, { useState } from "react";
import {
  calculatePrintTime,
  calculateFilamentUsage,
  calculateBudget,
} from "@/lib/utils";

interface SliceCalculatorProps {
  modelFile: File | null;
  parameters: {
    layerHeight: number;
    infillPercentage: number;
    support: boolean;
  };
  onResults: (results: {
    printTime: number;
    filamentWeight: number;
    budget: number;
  } | null) => void;
}

/**
 * Componente que ejecuta la simulación de corte (slice),
 * calcula tiempo, filamento y presupuesto, y muestra resultados.
 */
export default function SliceCalculator({
  modelFile,
  parameters,
  onResults,
}: SliceCalculatorProps) {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Función para simular el slicing y calcular resultados
  async function handleSlice() {
    setError(null);

    if (!modelFile) {
      setError("Por favor, sube un modelo 3D antes de cortar.");
      onResults(null);
      return;
    }

    setLoading(true);

    try {
      // Simulación: obtener volumen del modelo (en cm3)
      // Aquí se usa un valor fijo para demo, en producción se calcularía del modelo real
      const modelVolume = 50; // valor arbitrario para demo

      // Calcular tiempo de impresión
      const printTime = calculatePrintTime(modelVolume, parameters);

      // Calcular uso de filamento
      const filamentWeight = calculateFilamentUsage(modelVolume, parameters);

      // Calcular presupuesto
      const budget = calculateBudget(printTime, filamentWeight);

      // Enviar resultados al componente padre
      onResults({ printTime, filamentWeight, budget });
    } catch (err) {
      setError("Error al calcular el corte. Intenta de nuevo.");
      onResults(null);
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="mt-4">
      <button
        onClick={handleSlice}
        disabled={loading}
        className="w-full bg-black text-white py-3 rounded hover:bg-gray-800 transition"
      >
        {loading ? "Calculando..." : "Cortar Ahora"}
      </button>

      {/* Mostrar error si existe */}
      {error && <p className="mt-2 text-red-600 font-medium">{error}</p>}
    </div>
  );
}
