/**
 * Función para calcular el tiempo estimado de impresión en horas
 * @param modelVolume Volumen del modelo en cm3 (supuesto)
 * @param parameters Parámetros de impresión seleccionados
 * @returns Tiempo estimado en horas
 */
export function calculatePrintTime(
  modelVolume: number,
  parameters: { layerHeight: number; infillPercentage: number; support: boolean }
): number {
  try {
    // Fórmula simplificada: tiempo base proporcional al volumen y altura de capa
    const baseTime = modelVolume * 0.5; // factor arbitrario para demo
    const layerFactor = 0.2 / parameters.layerHeight; // más capas = más tiempo
    const infillFactor = 1 + parameters.infillPercentage / 100; // más relleno = más tiempo
    const supportFactor = parameters.support ? 1.2 : 1; // soporte aumenta tiempo un 20%

    const time = baseTime * layerFactor * infillFactor * supportFactor;
    return time;
  } catch (error) {
    console.error("Error calculando tiempo de impresión:", error);
    return 0;
  }
}

/**
 * Función para calcular el peso estimado de filamento en gramos
 * @param modelVolume Volumen del modelo en cm3 (supuesto)
 * @param parameters Parámetros de impresión seleccionados
 * @returns Peso estimado en gramos
 */
export function calculateFilamentUsage(
  modelVolume: number,
  parameters: { layerHeight: number; infillPercentage: number; support: boolean }
): number {
  try {
    // Densidad típica del filamento PLA ~1.24 g/cm3
    const filamentDensity = 1.24;

    // Volumen efectivo considerando relleno y soporte
    const effectiveVolume =
      modelVolume * (parameters.infillPercentage / 100) +
      (parameters.support ? modelVolume * 0.1 : 0); // soporte 10% extra

    const filamentWeight = effectiveVolume * filamentDensity;
    return filamentWeight;
  } catch (error) {
    console.error("Error calculando uso de filamento:", error);
    return 0;
  }
}

/**
 * Función para calcular el presupuesto basado en tiempo y filamento
 * @param printTime Tiempo de impresión en horas
 * @param filamentWeight Peso de filamento en gramos
 * @returns Presupuesto en moneda local (ejemplo USD)
 */
export function calculateBudget(printTime: number, filamentWeight: number): number {
  try {
    const costPerHour = 5; // costo por hora de máquina
    const costPerGram = 0.1; // costo por gramo de filamento

    const budget = printTime * costPerHour + filamentWeight * costPerGram;
    return budget;
  } catch (error) {
    console.error("Error calculando presupuesto:", error);
    return 0;
  }
}
