"use client"; // Indica que este componente se renderiza en el cliente

import React, { useState } from "react";

// Importamos los componentes que crearemos
import ModelUploader from "@/components/ModelUploader";
import ThreeDModelViewer from "@/components/ThreeDModelViewer";
import ParameterSelector from "@/components/ParameterSelector";
import SliceCalculator from "@/components/SliceCalculator";

/**
 * Página principal para la funcionalidad de slicing y presupuesto
 */
export default function SlicePage() {
  // Estado para el archivo 3D cargado
  const [modelFile, setModelFile] = useState<File | null>(null);

  // Estado para los parámetros seleccionados por el usuario
  const [parameters, setParameters] = useState({
    layerHeight: 0.2,
    infillPercentage: 20,
    support: false,
  });

  // Estado para los resultados del slicing
  const [sliceResults, setSliceResults] = useState<{
    printTime: number;
    filamentWeight: number;
    budget: number;
  } | null>(null);

  return (
    <main className="min-h-screen bg-white text-black p-6 font-sans">
      <h1 className="text-4xl font-bold mb-6 text-center">
        Simulador de Corte y Presupuesto de Impresión 3D
      </h1>

      <div className="flex flex-col md:flex-row gap-6">
        {/* Sección izquierda: carga de modelo y selección de parámetros */}
        <section className="md:w-1/3 space-y-6">
          <ModelUploader onModelUpload={setModelFile} />
          <ParameterSelector parameters={parameters} onChange={setParameters} />
          <SliceCalculator
            modelFile={modelFile}
            parameters={parameters}
            onResults={setSliceResults}
          />
        </section>

        {/* Sección derecha: visor 3D y resultados */}
        <section className="md:w-2/3">
          <ThreeDModelViewer modelFile={modelFile} />
          {sliceResults && (
            <div className="mt-6 p-4 border border-gray-300 rounded-lg bg-gray-50">
              <h2 className="text-2xl font-semibold mb-4">Resultados del Corte</h2>
              <p>Tiempo de impresión estimado: {sliceResults.printTime.toFixed(2)} horas</p>
              <p>Filamento estimado: {sliceResults.filamentWeight.toFixed(2)} gramos</p>
              <p>Presupuesto estimado: ${sliceResults.budget.toFixed(2)}</p>
            </div>
          )}
        </section>
      </div>
    </main>
  );
}
